.toggle_button {
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    height:15px;
    width:30px;
    background:transparent;
    border:none;
    padding:0;
    box-sizing: border-box;
}

.toggle_button:focus{
    outline:none;
}

.toggle_button_line{
    width:20px;
    height:1px;
    background:white;
}
